footer {
  width: 100%;
  padding: 40px 0 5px 0;
}
.footer-menu-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 500px) {
  .footer-menu-row {
    justify-content: flex-start;
  }
}

.footer-menu-row > div {
  width: 200px;
  margin: 20px 20px;
}

/*text */
.footer-menu-row h6 {
  font-size: 18px;
  font-weight: 400;
  margin: 0 0 10px 0;
}
.footer-menu-row ul {
  list-style: none;
}

.footer-menu-row a {
  text-decoration: none;
  color: #666666;
  font-weight: 400;
  margin: 0 0 5px 0;
  display: inline-block;
}
.footer-menu-row a:hover {
  color: #525659;
}

.social-media-footer {
  margin: 10px 0 0 0;
}
.social-media-footer a .fab {
  font-size: 25px !important;
  margin-right: 5px;
}
.social-media-footer .underline-dg {
  margin: -10px 0 10px 0;
  width: 145px;
}

/*copyright */
.copyright-container {
  border-top: 1px solid white;
  margin: 40px auto 0 auto !important;
  font-size: 14px;
}
.copyright-container > div {
  margin: 10px 0;
  font-family: "work-sans", sans-serif !important;
}

/*subscription are*/
.subscribe-section {
  background-color: #efeae5;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 0px 10px 10px 10px;
  margin-top: 0;
}
.subscribe-section > div {
  margin-top: 10px;
}
.subscribe-section .form .mc4wp-form-fields {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.subscribe-section .form .mc4wp-form-fields input[type="submit"] {
  width: 100px !important;
  background: #303030;
  color: white;
  cursor: pointer;
}
.subscribe-section .form .mc4wp-form-fields input[type="submit"]:hover {
  background: white;
  color: #303030;
  outline: 1px solid #303030;
}
.subscribe-section .form .mc4wp-form-fields input {
  padding: 15px;
  width: 250px;
  border: none;
}
@media (max-width: 900px) {
  .subscribe-section {
    justify-content: flex-start;
  }
}
/*footer img */
.footer-img img {
  display: block;
  width: 300px;
  margin: 0 auto;
}

/* arrow to the header */
.go-to-header {
  position: fixed;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
  z-index: 10000;
}
.go-to-header a i {
  font-size: 40px;
  background: rgba(0, 0, 0, 0.5);
  padding: 0px 10px;
  color: white;
}
.show {
  display: block !important;
}
.hide {
  display: none;
}
